import React, { useRef } from "react";
import { useNavigate, json } from "react-router-dom";
import { ref, child, get } from "firebase/database";
import { db } from "firebase-config";
import styled from "styled-components";

function SignInPage() {
  const navigate = useNavigate();
  const userKeyRef = useRef<HTMLInputElement>(null);

  const handleSignUp = () => {
    navigate("/signup");
  };

  const handleSignIn = async () => {
    const userKey = userKeyRef.current?.value;

    const dbRef = ref(db);
    const data = await get(child(dbRef, `users/${userKey}/userKey`))
      .then((snapshot) => {
        if (snapshot.exists()) {
          return snapshot.val();
        } else {
          window.alert("No data available.");
        }
      })
      .catch((error) => {
        throw json({ message: error }, { status: 500 });
      });

    if (userKey === data) {
      localStorage.setItem("userKey", userKey!);
      navigate("/");
    } else {
      window.alert("User Key .    .");
    }
  };

  return (
    <Container>
      <h1>
        . <br />
         .
      </h1>
      <div>
        <label>
           
          <input ref={userKeyRef} type="text" />
        </label>
        <button onClick={handleSignIn}>Done</button>
      </div>
      <button onClick={handleSignUp}> </button>
    </Container>
  );
}

export default SignInPage;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  gap: 30px;

  & div button {
    margin-top: 10px;
  }

  & div label {
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  & button {
    width: 200px;
  }
`;
